import { mapState } from 'vuex'
export default {
    name: 'statisticsPage',
    computed: {
        ...mapState(['userInfo']),        
        startDate() {
            return this.getDate('start');
        },
        endDate() {
            return this.getDate({
                format: true
            })
        }
    },
    data () {
        const currentDate = this.getDate({
            format: true
        })
        return {
            todayCount: 0,
            allCount: 0,
            listArr: [],
            pageNum:1,
            pageSize: 100,
            verifyDate: '',
            username: '',
            date: currentDate,
            searchContent: ''
        }
    },
    created() {
       this.getStatisticsList(true)
       this.getAllCount()
    },
    methods: {
        async getStatisticsList (bolean) {
            let time = new Date(this.date).getTime() - 8*60*60*1000
            let params = {
                pageNum : this.pageNum,
                pageSize : this.pageSize,
                verifyUsername : this.userInfo.username,
                username: this.searchContent,
                verifyDate : time
            }
            const res = await this.$api.getStatisList(params)
            if (res.code === '0') {                
                this.listArr = res.data.list
                if (bolean) {
                    this.todayCount = res.data.total
                }
            }
        },
        async getAllCount () {
            let params = {
                verifyUsername: this.userInfo.username
            }
            const res = await this.$api.getAllCount(params)
            if (res.code === '0') {
                this.allCount = res.data
            }
        },
        bindDateChange(e) {
            this.searchContent = ''
            this.date = e.target.value
            this.getStatisticsList(true)
        },
        search () {            
            if (!this.checkPhone(this.searchContent)) {
                this.$tip.toast('请填写完整的用户手机号','none')
                return
            }
            this.getStatisticsList(false)
        },
        checkPhone (phone) {
            if(!(/^1[34578]\d{9}$/.test(phone))){ 
                return false    
            }
            return true
        },
        getDate(type) {
            const date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();

            if (type === 'start') {
                year = year - 5;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
            return `${year}-${month}-${day}`;
        }
    },
}
